body {
  overflow: hidden;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}

.lido-disable-check-button {
  pointer-events: none;
  background-color: #9393935c !important;
  color: white;
}

.lido-element-selected{
  border: 2px solid;
  background-color: #ffdf7d !important;
}
